<template>
  <div style="width:100%">
    <form novalidate @submit.stop.prevent="submit">

        <md-input-container>
       <label>Topic Name</label>
        <md-input v-model='newTopic.name'></md-input>
       </md-input-container>
      <md-input-container>
       <label>Topic Description</label>
        <md-input v-model='newTopic.description'></md-input>
       </md-input-container>
        <md-input-container>
          <label>Order</label>
          <md-input v-model="newTopic.order" type="number"></md-input>
         </md-input-container>    
  
       <md-button @click.native="saveTopic()" class="md-fab">
        <md-icon>save</md-icon>
       </md-button>
       <md-button @click.native="cancel()" class="md-fab">
        <md-icon>cancel</md-icon>
       </md-button>
    </form>
  

 </div>
</template>

<script>
import {eventBus} from '../global.js'

export default {
  // componenents: {: Compname},
  name: 'topic',
  created: function () {
    console.log('topic create newTopic', this.newTopic)
    // created
  },
  props: ['newTopic', 'category'],
  mounted () {
    this.newTopic.categoryID = this.category.fbKey
    /*
    this.newTopic._parentPath = this.category.apiPath  + '/' + this.category.fbKey + '/'
    this.newTopic._parentRecord = this.category.apiPath + '.records[' + this.category.fbKey + ']'
    let parentRecord = this.apiPath + '.records[' + key + ']'
    let parentPath = this.apiPath + '/' + key + '/'
    */
    console.log('mounted create this.category ', this.category.fbKey)
    console.log('mounted create newTopic', this.newTopic)
    // console.log('mounted')
  },
  updated () {
    // console.log('updated')
  },
  data: function () {
    return {
      updated: false
    }
  },
  methods: {
    saveTopic () {
      eventBus.$emit('save-topic', this.newTopic)
      console.log('$emit(save-topic)', this.newTopic, this.newTopic.categoryID)
    },
    cancel () {
      eventBus.$emit('cancel-topic')
    }
  },
  destroyed: function () {
    //  code here
  }
}
</script>

