<template>
  <div style="width:100%">
  <div class="wrapper">
  <div class="flip-container">
    <div class="flipper"  v-bind:class="{clicked: isBack}">
      <div class="front">
      
        <md-card class="box-card">
        <md-card-header>
     
          <md-toolbar v-bind:class="[{'md-primary': isJS() },{'html': isHTML() }]" >
            <h2 class="md-title" style="flex: 1">
             <md-input-container>
               <md-input class="md-title" v-model="card.title" placeholder="title">
               </md-input>
             </md-input-container> 
            </h2>    
        
         <div class="field-group">
             <md-button  @click.native='rotateCard()'>

                <md-icon :style="{color: flipColor}">flip_to_back</md-icon>
              </md-button>
          <md-button class="md-fab md-clean md-mini" @click.native='saveCard()'  v-bind:disabled="disableSave">
                <md-tooltip md-direction="bottom">Save changes</md-tooltip>
            <md-icon>save</md-icon>
          </md-button>
          <md-button class="md-fab md-clean md-mini" @click.native='deleteCard()'>
                <md-tooltip md-direction="bottom">Delete card</md-tooltip>
            <md-icon>delete</md-icon>
          </md-button>
             <!--  <md-button  @click.native='jscode=formatJSCode(jscode)'>
                <md-icon>format_line_spacing</md-icon>
              </md-button>-->

          </md-toolbar>
        </md-card-header>
        <md-card-content >
          <codemirror v-model="card.code" :options="editorOption" > 
          </codemirror> 

            <input v-model="card.url" placeholder="link" style="width:100%"></input>
            
            <a v-if="card.url" :href="card.url" target="_blank">Click here</a>
            <!-- EO theme: {{editorOption.theme}}: mode: {{editorOption.mode}} -->
        </md-card-content>
        <md-card-actions>
            <md-input-container>
            <label for="type">Type</label>
            <md-select name="type" id="type" v-model="card.type">
              <md-option value="JS">JS</md-option>
              <md-option value="HTML">HTML</md-option>
              <md-option value="PYTHON">PYTHON</md-option>   
            </md-select>
          </md-input-container>
            <md-button @click.native='card.code = formatCode(card)' class="md-fab md-clean md-mini">
                 <!--  <md-button v-clipboard="card.code" class="md-fab md-clean md-mini "> -->
                <md-tooltip md-direction="right">format code</md-tooltip>
                <md-icon>text_format</md-icon>
              </md-button>
         </md-card-actions>  
      </md-card>
    </div><!-- front -->

    <div class="back">
        <md-card class="box-card">
          <md-card-header>
          
          <md-toolbar style="background-color: lightslategrey">
            <h2 class="md-title" style="flex: 1"> 
            <md-input-container>
            <md-input class="md-title" v-model="card.backtitle" placeholder="back title">
             
            </md-input>          
            </md-input-container>
            </h2>
        
              <md-button  @click.native='rotateCard()'>
                <md-icon>flip_to_front</md-icon>
              </md-button>
              <md-button v-clipboard="card.code" class="md-fab md-clean md-mini ">
                <md-tooltip md-direction="bottom">Copy code</md-tooltip>
                <md-icon>content_copy</md-icon>
              </md-button>
            </md-toolbar>
        </md-card-header>
        <md-card-content>
         <!-- <md-input-container> -->
             <!-- <md-textarea rows="7" v-model="card.backtext"></md-textarea> -->
          
           <textarea rows="7" v-model="card.backtext" style="width:100%"></textarea>
           <!--</md-input-container>-->
            <h6> topic id: {{card.topicID}} card key: {{card.key}} </h6>
       
        </md-card-content>
         <md-card-actions>
          <md-button class="md-fab md-clean md-mini" @click.native='saveCard()'  v-bind:disabled="disableSave">>
                <md-tooltip md-direction="bottom">Save changes</md-tooltip>
            <md-icon>save</md-icon>
          </md-button>

     
         <!--  !!!! problem code
          <md-speed-dial md-mode="scale" class="md-fab-bottom-right">
             <md-button class="md-fab md-clean md-mini" @click.native='saveCard()'>
               <md-tooltip md-direction="bottom">Save changes</md-tooltip>
                <md-icon>save</md-icon>
           </md-button>
            </md-speed-dial>-->
       
         </md-card-actions>  
      </md-card>
      </div><!-- back -->
    </div><!-- flipper -->
  </div><!-- flip-container -->
 </div><!-- wrapper -->

</div>
</template>

<script>
import {eventBus} from '../global.js'
import beautifyJS from 'js-beautify'
var beautifyHtml = require('js-beautify').html
// var beautifCss = require('js-beautify').css;

export default {
  // componenents: {compname: Compname},
  name: 'codecard',
  created: function () {
    // created
    this.card.code = this.formatCode(this.card)
  },
  props: ['card', 'container'],
  mounted () {
    // console.log('mounted')
  },
  updated () {
    // console.log('updated')
  },
  watch: {
    'card': {
      handler (val, oldval) {
        // console.log('card deep...')
        // console.log('val', val)
        // console.log('oldval', oldval)
        if (val === oldval) {
          this.disableSave = false
        } else {
          this.disableSave = true
        }
      },
      deep: true
    },
    'compcard': function (val, oldval) {
      // console.log('compcard', val, oldval)
    },
    'card.type': function (val) {
      console.log('watch card type ', val)
    },
    'card.code': function (val, oldval) {
      // console.log('watch card code ', val, oldval)
    }
  },
  computed: {
    flipColor: function () {
      return this.card.backtext ? 'yellow' : ''
    },
    editorOption () {
      var opts = {
        tabSize: 2,
        mode: 'text/javascript', // text/javascript, text/x-vue, text/x-python
        theme: 'monokai', // night, panda-syntax, seti,xq-dark, neat, elegant', 'rubyblue', 'monokai'
        lineNumbers: false,
        line: true,
        keyMap: 'sublime',
        extraKeys: { 'Ctrl': 'autocomplete' },
        foldGutter: true,
        // height: 100,
        gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
        styleSelectedText: true,
        highlightSelectionMatches: { showToken: /\w/, annotateScrollbar: true }
        // viewportMargin: infinity
      }
      if (this.isHTML()) {
        opts.mode = 'text/html'
        opts.theme = 'neat'
      }
      if (this.isPYTHON()) {
        opts.mode = 'text/x-python'
        opts.theme = 'base16-light'
        opts.keyMap = 'emacs'
        console.log(opts.mode)
      }
      return opts
    },
    editorOpt () {
      if (this.isJS()) {
        return {
          tabSize: 2,
          mode: 'text/javascript', // text/javascript, text/x-vue, text/x-python
          theme: 'night', // night, panda-syntax, night,seti,xq-dark, xq-light, cobalt, neat, eclipse, elegant', 'rubyblue', 'monokai'
          lineNumbers: false,
          line: true,
          keyMap: 'sublime',
          extraKeys: { 'Ctrl': 'autocomplete' },
          foldGutter: true,
          // height: 100,
          gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
          styleSelectedText: true,
          highlightSelectionMatches: { showToken: /\w/, annotateScrollbar: true }
          // viewportMargin: infinity
        }
      } else {
        return this.htmlOption
      }
    },
    codeOptions () {
      var option = {
        theme: 'night',
        mode: 'text/javascript'
      }
      if (this.isHTML()) {
        option.theme = 'neat'
        option.mode = 'text/html'
      }
      if (this.isPYTHON()) {
        option.theme = 'neat'
        option.mode = 'Text/X-Python'
      }
      return option
    },
    currentUser () {
      // console.log('computed username')
    },
    jscodecomputed () {
      return this.jscode
    },
    compcard () {
      return this.card
    }
  },
  data: function () {
    return {
      formatTypes: ['JS', 'HTML'],
      disableSave: true,
      jscode: '',
      cardchecked: false,
      cardData: [],
      cardTest: this.card,
      isBack: false,
      dataLoading: false,
      url: 'https://vuejs.org/v2/guide/instance.html#Lifecycle-Diagram'
    }
  },
  methods: {
    isJS () {
      if (this.card.type === 'JS') {
        return true
      } else {
        return false
      }
    },
    isHTML () {
      if (this.card.type === 'HTML') {
        return true
      } else {
        return false
      }
    },
    isPYTHON () {
      if (this.card.type === 'PYTHON') {
        return true
      } else {
        return false
      }
    },
    setHidden () {
      eventBus.$emit('hide-message')
    },
    saveCard () {
      this.card.code = this.formatCode(this.card)
      console.log('$emit(save-card)', this.card)
      // eventBus.$emit('save-card', this.card)
      this.container.event.$emit('save-card', this.card)
      this.disableSave = true
    },
    deleteCard () {
      // eventBus.$emit('delete-card', this.card)
      this.container.event.$emit('delete-card', this.card)
    },
    rotateCard () {
      this.isBack = !this.isBack
    },
    formatCode (card) {
      if (this.formatTypes.indexOf(card.type) >= 0) {
        if (card.type === 'JS') {
          return this.formatJSCode(card.code)
        }
        if (card.type === 'HTML') {
          return this.formatHTMLCode(card.code)
        }
      } else {
        return card.code
      }
    },
    formatJSCode (code) {
      return beautifyJS(code, {
        'indent_size': 2,
        'indent_char': ' ',
        'eol': '\n',
        'end_with_newline': true,
        'other': ' ',
        'indent_level': 0,
        'indent_with_tabs': false,
        'preserve_newlines': true,
        'max_preserve_newlines': 2,
        'jslint_happy': true,
        'indent_handlebars': true,
        'object': {}
      })
    },
    formatHTMLCode (code) {
      return beautifyHtml(code, {
        'indent_size': 2,
        'indent_char': ' ',
        'eol': '\n',
        'end_with_newline': true,
        'other': ' ',
        'indent_level': 0,
        'indent_with_tabs': false,
        'preserve_newlines': true,
        'max_preserve_newlines': 2,
        'jslint_happy': false,
        'indent_handlebars': true,
        'wrap_line_length': 30,
        'object': {}
      })
    }
  },
  destroyed: function () {
    //  code here
  }
}
</script>

<style scoped>
 .js {
   background: blue;
 }
 .html {
   background: green !important;
 }
 .CodeMirror {
   height: 200px !important;
 }
 .CodeMirror-code {
   line-height: 1.2em !important;
   /*font-size: 12px !important;*/
 }
 .box-title {
  /*background-color: blue;*/
    color: white;
  }
  .box-card {
   /* width: 480px; */
   /* width: 100%; */
    min-height: 450px;
    text-overflow: ellipsis;
    /*overflow: scroll;*/
  }
.flip-container {
  perspective: 1000;
}

/*.flip-container:hover .flipper {
  transform: rotateY(180deg);
}*/
.flipper.clicked {
  transform: rotateY(180deg);
}
.flip-container, 
.front, 
.back {
    /*width: 300px;*/
    height: 450px;
    width: 100%;
}

.flipper {
    transition: 0.6s;
    transform-style: preserve-3d;
    position: relative;
}

.front, 
.back {
    backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
}

.front {
  z-index: 2;
}

.back {
  transform: rotateY(180deg);
}

</style>



