<template>
  <div style="width:100%">
    <form novalidate @submit.stop.prevent="submit">
    
        <md-input-container>
       <label>Category Name</label>
        <md-input v-model='newCategory.name'></md-input>
       </md-input-container>
      <md-input-container>
       <label>Category Description</label>
        <md-input v-model='newCategory.description'></md-input>
       </md-input-container>
       <md-input-container>
          <label>Order</label>
          <md-input v-model="newCategory.order" type="number"></md-input>
         </md-input-container>    
  
       <md-button @click.native="saveCategory()" class="md-fab">
        <md-icon>save</md-icon>
       </md-button>
       <md-button @click.native="cancel()" class="md-fab">
        <md-icon>cancel</md-icon>
       </md-button>
    </form>
    {{newCategory.description}}

 </div>
</template>

<script>
import {eventBus} from '../global.js'

export default {
  // componenents: {: Compname},
  name: 'category',
  created: function () {
    // created
  },
  props: ['newCategory'],
  mounted () {
    // console.log('mounted')
  },
  updated () {
    // console.log('updated')
  },
  data: function () {
    return {
      updated: false
    }
  },
  methods: {
    saveCategory () {
      eventBus.$emit('save-category', this.newCategory)
      console.log('$emit(save-category)', this.newCategory)
    },
    cancel () {
      eventBus.$emit('cancel-category')
    }
  },
  destroyed: function () {
    //  code here
  }
}
</script>

<style scoped>
div {
   background: lightblue;
}
</style>



