// @ts-check
import Vue from 'vue'

// const eventCodeCards = new Vue()
import {eventBus} from '../global.js'
import _ from 'lodash'

import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)
const HTTPAxios = Vue.axios.create()
/*
class VueEvent {
  constructor () {
    this.eventBus = new Vue()
  }
}
*/
class BaseAPI {
  constructor (APIname, rootURL) {
    this.APIname = APIname
    this.rootURL = rootURL
  }
}
class BaseField {
  constructor () {
    this.fieldName = ''
    this.fieldType = ''
    this.info = 'field metadata'
  }
}
class BaseFields {
  constructor () {
    this.aFields = []
    this.info = 'list of fields in aFields'
  }
  fieldCount () {
    return this.aFields.length
  }
}
class BaseRecord {
  constructor (data, key) {
    this.record = data
    this.edited = false
    this.info = 'single record stored in this.data'
    this.key = key
    this.processing = 0
  }
}
// TO-DO add child records, e.g., topics , add key
class BaseRecords {
  constructor (idField, apiPath, apiBaseURL, httpMethod) {
    this.event = {}
    // this.eventOn = function () {}
    // this.eventEmit = function () {}
    this.idField = idField
    // this.keyField = keyField
    this.records = []  // these are the data records, of class BaseRecord
    this.childRecords = [] // subrecord names
    this.apiBaseURL = apiBaseURL
    this.apiPath = apiPath
    this.fields = new BaseFields()
    this.info = 'Records array stored in records'
    this.name = ''
    this.recordClass = BaseRecord
    this.processing = 0
    if (httpMethod) {
      this.HTTP = httpMethod
    } else {
      this.method = HTTPAxios
    }
  }
  postRecord (record, customPath) {
    var key
    let vm = this
    console.log('BaseRecords postRecord')
    let path = ''
    if (customPath) {
      path = customPath
    } else {
      path = this.apiBaseURL + this.apiPath + '.json'
    }
    vm.HTTP.post(path, record)
      .then(function (response) {
        key = response.data.name
        record.key = key
        Vue.set(this.records, key, record)
        console.log('eventBus.$emit(post-complete)')
        eventBus.$emit('post-complete', vm.idField)
      })
  }
  updateRecord (record, key, customPath) {
    let path = ''
    if (customPath) {
      path = customPath
    } else {
      this.apiBaseURL + this.apiPath + '/' + key + '.json'
    }
    // this.$http.patch('https://vue-helper.firebaseio.com/codecards/' + cardkey + '.json', card)
    Vue.axios.patch(path, record)
       .then(function (response) {
         console.log('updated record with key', key)
       })
  }
  loadFromArray (arrayData) {
    var _this = this
    arrayData.forEach(function (item, index, array) {
      _this.records.push(new BaseRecord(item, index))
    })
  }
  loadFromObject (oData) {
    var _this = this
    _this.records = oData
    for (var key in _this.records) {
      _this.records[key].key = key
    }
  }
  getRecords (params, customPath) {
    var vm = this
    let path = ''
    if (customPath) {
      path = customPath
    } else {
      path = this.apiPath
    }
    vm.processing = 1
    vm.clearRecords()
    vm.HTTP.get(this.apiBaseURL + path, {
      params: params
    })
    .then(function (response) {
      var data = response.data
      if (Array.isArray(data)) {
        vm.loadFromArray(data)
      } else {
        vm.loadFromObject(data)
      }
      vm.processing = 0
      console.log('BaseRecords.records', vm.records)
    })
    .catch(function (error) {
      console.log(error)
      vm.processing = -1
    })
  }
  newRecord (data) {
    this.records.push(new BaseRecord(data))
  }
  clearRecords () {
    this.records = []
  }
  recordCount () {
    return this.records.length
  }
}
export class Record extends BaseRecord {
  constructor () {
    // Here, it calls the parent class' constructor with lengths
    // provided for the Polygon's width and height
    super()
    // Note: In derived classes, super() must be called before you
    // can use 'this'. Leaving this out will cause a reference error.
    this.name = 'Record'
  }
}

/*
  var records = this.categories.records[newTopic.categoryID].topics
                this.apiPath + '.records[parentID]' + '.' + childName
  var customPath = 'categories/' + newTopic.categoryID + '/topics.json'
  this.categories.postRecord(newTopic, 'categories.topics', customPath , records)
  updateRecord() path = this.apiBaseURL + this.apiPath + '/' + fbKey + '.json'
*/

class FBJSONRecords extends BaseRecords {
  constructor (idField, apiPath, apiBaseURL, httpMethod, childRecords) {
    super(idField, apiPath, apiBaseURL, httpMethod)
    // Note: In derived classes, super() must be called before you
    // can use 'this'. Leaving this out will cause a reference error.

    /* event is a Vue specific event handler, .$on(a,b) and $emit(a,b) */
    this.event = new Vue()
    this.info = 'Firebase JSON Records'
    // this.childRecords = {'topics': {'parentIDField': 'categoryID'}, 'key2': {}}
    this.childRecords = childRecords
  }
  postRecord (record, recordName, parentKey) {
    var fbKey
    let vm = this
    let path = this.apiBaseURL + this.apiPath + '.json'

    let records = vm.records   // class default records
    let parentPath = ''
    let parentRecord = ''
    let rs = ''
    if (parentKey) {
      parentPath = this.apiPath + '/' + parentKey + '/'
      path = vm.apiBaseURL + parentPath + recordName + '.json'
      parentRecord = 'records[' + parentKey + ']'
      rs = parentRecord + '.' + recordName
      // child records
      records = _.get(vm, rs)
    }
    // this.newTopic._parentPath = this.category.apiPath  + '/' + this.category.fbKey + '/'
    // this.newTopic._parentRecord = 'records[' + this.category.fbKey + ']'
    /*
    if (parentPath) {
      path = this.apiBaseURL + parentPath + recordName + '.json'
    } else {
      path = this.apiBaseURL + this.apiPath + '.json'
    }
    */
    vm.processing = 1
    Vue.axios.post(path, record)
      .then(function (response) {
        fbKey = response.data.name
        record.fbKey = fbKey
        /* add properties to child record */
        if (parentPath) {
          record._parentPath = parentPath
          // ._parentRecord = "records[-KeJjoXFANIHGT16WjcG]"
          record._parentRecord = parentRecord
        }
        // custompath 'categories/' + newTopic.categoryID + '/topics.json'
        //  vm.categories.records[key].topics[tKey].child2
        // child record example: Vue.set(vm.categories.records[key].topics, key, newTopic)
        // Vue.set(vm.records, fbKey, record)
        console.log('records: ', records)
        Vue.set(records, fbKey, record)
        vm.event.$emit('post-complete', recordName)
        vm.processing = 0
      })
      .catch(function (error) {
        console.log(error)
        vm.processing = -1
      })
  }
  deleteRecord (record, recordName) {
    let path = ''
    let fbKey = record.fbKey
    let vm = this
    if (record._parentPath) {
      path = this.apiBaseURL + record._parentPath + recordName + '/' + fbKey + '.json'
    } else {
      path = this.apiBaseURL + this.apiPath + '/' + fbKey + '.json'
    }
    let records = vm.records   // class default records
    if (record._parentRecord) {
      let rs = record._parentRecord + '.' + recordName
      // categories.records[-KdkRwRFlto4ErykIaWs].topics
      records = _.get(vm, rs)
      console.log('deleteRecord records[fbKey] ', records[fbKey])
    }
    // this.$http.patch('https://vue-helper.firebaseio.com/codecards/' + cardkey + '.json', card)
    Vue.axios.delete(path, record)
       .then(function (response) {
         records[fbKey] = null
         delete records[fbKey]
       })
       .catch(function (error) {
         console.log(error)
       })
  }
  updateRecord (record, fbKey, recordName) {
    let path = ''
    // let vm = this
    if (record._parentPath) {
      path = this.apiBaseURL + record._parentPath + recordName + '/' + fbKey + '.json'
    } else {
      path = this.apiBaseURL + this.apiPath + '/' + fbKey + '.json'
    }
    // this.$http.patch('https://vue-helper.firebaseio.com/codecards/' + cardkey + '.json', card)
    Vue.axios.patch(path, record)
       .then(function (response) {
         console.log('updated record with key', fbKey)
       })
       .catch(function (error) {
         console.log(error)
         // vm.processing = -1
       })
  }
  loadFromObject (oData) {
    // for Firebase Object{key: Object, key: Object}
    // ** _this.records.push(new BaseRecord(oData))
    // _this.records.push(oData)
    // for Firebase, this method loses the outer keys
    this.records = oData
    for (var key in this.records) {
      // Add key to record.key
      try {
        this.records[key].fbKey = key
      } catch (err) {
        console.log('error adding fbKey for key ', key)
      }
      // detect if property is object (child)
      for (var fld in this.records[key]) {
        if (typeof this.records[key][fld] === 'object') {
          console.log('fld is object: ', fld)
        }
      }
      // if childRecords defined, add fbKey to each record
      if (this.childRecords) {
        for (var childName in this.childRecords) {
          // console.log('childName ', childName)
          /*
          let childRecord = this.childRecords[childName]
          // childRecord fields
          for (var k in childRecord) {
            console.log('k', k)
            console.log('childRecord[k]', childRecord[k])
          }
          */
          /* TODO create record for each childTopic */
          for (var childKey in this.records[key][childName]) {
            try {
              this.records[key][childName][childKey].fbKey = childKey
              // let parentRecord = this.apiPath + '.records[' + key + ']'
              let parentRecord = 'records[' + key + ']'
              let parentPath = this.apiPath + '/' + key + '/'
              // console.log('parentPath', parentPath)
              this.records[key][childName][childKey]._parentPath = parentPath
              this.records[key][childName][childKey]._parentRecord = parentRecord
            } catch (err) {
              console.log('error adding fbKey for childKey ', childKey)
            }
          }
        }
      }
    }
  }
  getRecords (params, customPath) {
    var vw = this
    let path = ''
    this.processing = 1
    if (customPath) {
      path = customPath
    } else {
      path = this.apiPath
    }
    this.clearRecords()
    vw.HTTP.get(path, {
      params: params
    })
    .then(function (response) {
      var data = response.data
      console.log('baseClasses vw.HTTP.get ', data)
      if (data) {
        vw.loadFromObject(data)
      }
      vw.processing = 0
    })
    .catch(function (error) {
      console.log(error)
      vw.processing = -1
    })
  }
}
export {BaseAPI, BaseRecords, BaseRecord, BaseField, BaseFields, FBJSONRecords}
/*
exports.BaseAPI = BaseAPI
exports.BaseRecord = BaseRecord
exports.BaseRecords = BaseRecords
exports.BaseField = BaseField
exports.BaseFields = BaseFields
*/
