<template>
  <div style="width:100%">

    <draggable :list="list" :options="{name:'topic'}">    

 
       <div v-for="(item, key) in list" >
            <div class="borderlist" @dblclick='showEdit = !showEdit'>
              <span class="step"> {{key+1}}</span>
                <md-icon class="md-primary">label_outline</md-icon>
                {{item.name}}  (original order: [{{item.order}}]) 
                <md-button class="md-icon-button md-raised md-dense md-mini" style="float: right">
                <md-icon>delete</md-icon>
              </md-button>
         
                 <div v-if="showEdit">
                 <md-input-container>
                 <md-input v-model='item.name'></md-input>
                 </md-input-container>
                </div> 
     
            </div>
     </div>       
    </draggable>
        <md-button @click.native="saveTopic()" class="md-fab">
        <md-icon>save</md-icon>
 </div>
</template>

<script>
import {eventBus} from '../global.js'
import draggable from 'vuedraggable'

export default {
  components: {
    draggable
  },
  created: function () {
    // created
  },
  props: ['topics', 'categoryaKey'],
  mounted () {
    console.log('topicEdit create newTopic', this.categoryKey)
    this.createList()
    // console.log('mounted')
  },
  updated () {
    // console.log('updated')
  },
  data: function () {
    return {
      updated: false,
      drag: true,
      list: [],
      showEdit: false
    }
  },
  methods: {
    createList () {
      var i = 0
      for (var key in this.topics) {
        i = i + 1
        this.list.push({name: this.topics[key].name, key: this.topics[key].key, order: i})
      }
      console.log('this.list ', this.list)
    },
    saveTopic () {
      console.log('saveTopic for catKey ', this.categoryKey)
      /*
      for (var key in this.list) {
        console.log('saveTopic this.list[key].name ', this.list[key].name, key)
      }
      */
      eventBus.$emit('update-topiclist', this.list, this.categoryKey)
      /* this.newTopic.categoryID = this.category.key
      eventBus.$emit('save-topic', this.newTopic)
      console.log('$emit(save-topic)', this.newTopic, this.newTopic.categoryID)
      */
    },
    cancel () {
      eventBus.$emit('cancel-topic')
    }
  },
  destroyed: function () {
    //  code here
  }
}
</script>

<style scoped>
.borderlist 
{
    border: 1px solid black;
    width: 50%;
    padding: 10px;
}
span.step {
  background: #cccccc;
  border-radius: 0.8em;
  -moz-border-radius: 0.8em;
  -webkit-border-radius: 0.8em;
  color: #ffffff;
  display: inline-block;
  font-weight: bold;
  line-height: 1.6em;
  margin-right: 5px;
  text-align: center;
  width: 1.6em; 
}
</style>



