import Vue from 'vue'
import {BaseAPI, BaseRecords, FBJSONRecords} from './baseClasses'
console.log('test ', BaseRecords)
var api = new BaseAPI('axiosfb', 'https://vue-helper.firebaseio.com/')
const HTTP1 = Vue.axios.create()
HTTP1.defaults.baseURL = 'https://vue-helper.firebaseio.com/'

export var categories = new FBJSONRecords('categoryID', 'categories', api.rootURL, HTTP1, {'topics': {'parentIDField': 'categoryID'}})
// this.categories.childRecords = this.childRecords = {'topics': {'parentIDField': 'categoryID'}}
// Vue.axios.get('https://vue-helper.firebaseio.com/codecards.json')
export var codeCards = new FBJSONRecords('cardID', 'codecards', api.rootURL, HTTP1)
