<template>
  <!--<div> -->

 <md-layout :md-gutter="8"> 
  <!-- left navigation  -->
  <md-layout md-column md-flex="25" v-show="!hideMenu" > 
     <md-toolbar> 
      <div class="md-toolbar">
         <h2 class="md-title" style="flex: 1">Categories</h2>
       
         <md-button @click.native='getCategories()' class="md-fab md-mini md-clean">
           <md-icon>refresh</md-icon>
         </md-button>
         <md-button @click.native="deleteTopic()" class="md-fab md-mini md-clean">
           <md-icon>delete</md-icon>
         </md-button>

       </div>
    </md-toolbar>
    <md-input-container>
    <label for="category">Category</label>


    <md-select v-model="category" @change="getTopicsForCategory()">
     <md-option v-for="catItem in categoriesOrdered"  v-bind:value="catItem">
      {{ catItem.name }}
    </md-option>
   </md-select>
  <!-- <span>Selected: {{ category }}</span> -->
  </md-input-container>
  <div style="height:400px; overflow:scroll">
           
            <md-list style="padding:0 0" v-for="(topic, topkey) in category.topics" :key="topic.fbKey">
            <md-list-item @click.native='setTopicCards(topic, topkey)' ref='topic' class="md-inset">{{topic.name}}
            </md-list-item>
          </md-list>
   </div>
    <!-- Categories -->
    <!-- <md-list class="md-dense md-theme-green" v-for="(category, catkey) in categories"> -->
    <!--  <div v-for="(value, key) in categories.records">
      outer key: {{key}}
       <div v-for="(v,k) in value.record">
      --> 

   
     <!--</md-whiteframe>-->
  
   <!-- speed dial button at bottom of navigation menu --> 
   <md-speed-dial md-mode="scale" class="md-fab-bottom-left">
    <md-button class="md-fab md-primary" md-fab-trigger>
      <md-icon md-icon-morph>close</md-icon>
      <md-icon>add</md-icon>
    </md-button>
     <md-button @click.native='topicEdit()' class="md-fab md-mini md-clean">
      <md-icon>edit</md-icon>
    </md-button>
    <md-button @click.native='addTopic()' class="md-fab md-mini md-clean">
      <md-icon>subject</md-icon>
    </md-button>
    <md-button @click.native='addCategory()' class="md-fab md-mini md-clean">
      <md-icon>toc</md-icon>
    </md-button>

   </md-speed-dial>
  </md-layout>  <!-- END left navigation  -->

  <!-- main display  --> 
  <md-layout md-column :md-gutter="8">
 
   <md-toolbar>
     <md-button-toggle class="md-warn" > 
      <md-button class="md-icon-button md-toggle" @click.native='hideMenu = !hideMenu'>
         <md-tooltip md-direction="bottom">Toggle menu display</md-tooltip>
        <md-icon>menu</md-icon>
      </md-button>
    </md-button-toggle>
      <h1> &nbsp;&nbsp;[{{category.name}}] </h1>
      <h2 v-if="showCards">&nbsp;&nbsp;  {{category.description}}: {{topic.description || topic.name}}</h2>
      <h2 v-if="showNewCategory">&nbsp;&nbsp;  New Category</h2>
      <h2 v-if="showNewTopic">&nbsp;&nbsp; New Topic for:  {{category.name}}</h2>
      <h2 v-if="showNewCard">&nbsp;&nbsp; New Card  [Topic: {{topic.name}}] </h2>
      <h2 v-if="showTopicEdit">&nbsp;&nbsp; Drag and Drop topic(s)</h2>
   </md-toolbar>  
 
   <md-layout :md-gutter="8">
     <!--  CARDS -->
     <md-layout style="height:450px" v-if="showCards"  v-for="(card, index) in cardsForTopic" :key="card.fbKey" md-flex='100' md-flex-xsmall='100' md-flex-small='100' md-flex-medium='100' md-flex-large='50' md-flex-xlarge='50'>
       <codecard :card="card" :container="codeCards">  </codecard>
     </md-layout>
     <md-layout v-if="showNewCard" md-flex='100' md-flex-xsmall='100' md-flex-small='100' md-flex-medium='100' md-flex-large='75' md-flex-xlarge='75' >
       <codecard :card="newCard" :container="codeCards">  </codecard>
     </md-layout>

     <!--</div>-->
     <div style="width:90%" v-if="showNewCategory">   
       <category :newCategory="newCategory"> </category>
     </div>  
     <div style="width:90%" v-if="showNewTopic">
      <topic :newTopic="newTopic" :category="category"> </topic>
     </div>  
      <div style="width:90%" v-if="showTopicEdit">   
       <topicedit :topics="category.topics" :categoryKey="catKey" > </topicedit>
     </div>  

   </md-layout>

  </md-layout> <!-- end main section -->

  <!-- Buttons at bottom of page -->
  <div v-if="showCards">
  <md-button @click.native="addCard()" class="md-fab md-fab-bottom-right">
  <md-tooltip md-direction="left">Add new card</md-tooltip> 
    <md-icon>add</md-icon>
  </md-button>
  </div>
</template>

<script>
import {eventBus} from '../global.js'
import codecard from 'components/Codecard.vue'
import category from 'components/Category.vue'
import topic from 'components/Topic.vue'
import topicedit from 'components/TopicEdit.vue'
// import Vue from 'vue'
import _ from 'lodash'
import {categories, codeCards} from '../classes/codecards'
// TO-DO
/*
  postCard
  updateCard
  postTopic
  postCategory
*/
export default {
  components: {
    codecard: codecard,
    category: category,
    topic: topic,
    topicedit: topicedit
  },
  created () {
    var _this = this
    this.getCategories()
    // * get categories data from firebase
    // var catPath = 'categories.json'
    // categories.getRecords('', catPath)
    _this.categories.event.$on('test-message', function (msg) {
      // console.log('test-message', msg)
    })
    eventBus.$on('hide-message', function () {
      // _this.showHideMessage()
    })
    _this.codeCards.event.$on('save-card', function (card) {
      if (_this.showNewCard) {
        _this.postCard(card)
        _this.resetCard()
      } else {
        _this.updateCard(card)
      }
    })
    _this.codeCards.event.$on('delete-card', function (card) {
     //  console.log('delete-card card', card)
      _this.codeCards.deleteRecord(card, 'codecards')
    })
    eventBus.$on('delete-topic', function () {
      // console.log('delete-topic topic', _this.topic)
      _this.deleteTopic(_this.topic, 'topics')
    })
    eventBus.$on('cancel-category', function () {
      _this.showNewCategory = false
      _this.showCards = true
      _this.resetCategory()
    })
    eventBus.$on('cancel-topic', function () {
      _this.showNewTopic = false
      _this.showCards = true
      _this.resetTopic()
    })
    eventBus.$on('save-topic', function (newTopic) {
      // console.log('$on(save-topic)')
      _this.postTopic(newTopic)
      _this.showCards = true
      _this.showNewTopic = false
    })
    eventBus.$on('update-topiclist', function (list, categoryKey) {
      // console.log('$on(update-topiclist)')
      _this.updateTopicList(list, categoryKey)
      _this.showCards = true
      _this.showTopicEdit = false
    })
    eventBus.$on('save-category', function (newCategory) {
      // console.log('$on(save-category)')
      _this.postCategory(newCategory)
      _this.showCards = true
      _this.showNewCategory = false
    })
    eventBus.$on('post-complete', function (recordName) {
      // console.log('eventBus$on(post-complete)', recordName)
      if (recordName === 'codecards') {
        _this.resetShow()
        _this.showCards = true
      }
      if (recordName === 'categories') {
        _this.resetCategory()
      }
      if (recordName === 'topics') {
        _this.resetTopic()
      }
    })
  },
  data () {
    return {
      // ** posts: new BaseRecords('userId'),
      showCards: false,
      showNewCategory: false,
      showNewTopic: false,
      showNewCard: false,
      showTopicEdit: false,
      data: {},
      dataArray: [],
      // ** aCategories: categories,
      categories: categories,  // categories.record
      codeCards: codeCards,
      category: [], // selected category
      catKey: [], // selected category key
      topics: [],
      topic: [],
      topicKey: '',
      cardsAll: [],  // all cards
      cards: [], // current topic cards
      card: [],  // selected card
      newCard: {
        cardID: '',
        backtext: '',
        backtitle: 'Notes',
        code: '',
        description: '',
        title: '',
        topicID: '',
        type: 'JS',
        key: '',
        fbKey: ''
      },
      newTopic: {
        description: '',
        name: '',
        topicname: '',
        fbKey: ''
      },
      newCategory: {
        description: '',
        name: '',
        topics: [],
        fbKey: ''
      },
      hideMenu: false // left menu display
    }
  },
  computed: {
    categoriesOrdered: function () {
      return _.orderBy(this.categories.records, ['order'], ['asc'])
    },
    cardsForTopic: function () {
      return _.filter(this.codeCards.records, {topicID: this.topicKey})
    },
    topicsForCategory: function () {
      return _.filter()
    }
  },
  methods: {
    updateTopicList (list, categoryKey) {
      let vm = this
      // console.log('updateTopicList ', list, categoryKey)
      for (var key in vm.categories.records) {
        // console.log(vm.categories.records[key])
        if (vm.categories.records[key].key === categoryKey) {
          // console.log('category is ', vm.categories.records[key])
          for (var k1 in list) {
            // console.log('list[k1]', list[k1])
            for (var k2 in vm.categories.records[key].topics) {
              if (list[k1].key === vm.categories.records[key].topics[k2].key) {
                vm.categories.records[key].topics[k2].name = list[k1].name
                vm.categories.records[key].topics[k2].order = k1 + 1
                console.log('new order', vm.categories.records[key].topics[k2].order)
              }
            }
          }
        }
      }
    },
    topicsForCategory (topic) {
      // *Filter topics by category
      // let topicCards = cardsAll.filter((card) => card.topicID === '-KeJjzCK0d7Pn36V0Hfx' )
      // return topics.filter((topic) => topic.categoryID === '-KcoJzmzezD0ByUT0z4L')
      return _.filter(topic, function (o) {
        // console.log('filter topics fo', o)
        return o.categoryID === '-KcoJzmzezD0ByUT0z4L'
        // return (o.name === 'Operators') & (o.categoryID !== '-KeJjoXFANIHGT16WjcG')
      })
      // return _.filter(topic, function(o) { return !o.active; })
      // return _.filter(topic, {categoryID: '-KcoJzmzezD0ByUT0z4L'})
      // Name: EventBus
      /*
      return topics.filter(function (categoryID) {
        return categoryID === '-KcoJzmzezD0ByUT0z4L'
      })
      */
    },
    resetCategory () {
      this.newCategory = {
        description: '',
        name: '',
        topic: []
      }
    },
    resetTopic () {
      this.newTopic = {
        description: '',
        name: '',
        topicname: '',
        fbKey: ''
      }
    },
    resetCard () {
      this.newCard = {
        backtext: '',
        backtitle: 'Notes',
        code: '',
        description: '',
        cardID: '',
        title: '',
        topicID: this.topic.key,
        type: 'JS'
      }
    },
    resetShow () {
      this.showCards = false
      this.showNewCategory = false
      this.showNewTopic = false
      this.showNewCard = false
      this.showTopicEdit = false
      // console.log('showcards = false')
    },
    selectCategory (category, catkey) {
      // if category new, set this.showCards = false
      // (workaround for issue for @click on menu)
      // this.categories.event.$emit('test-message', 'selectCategory')
      if (this.catKey !== catkey) {
        this.category = category
        // this.catKey = catkey // catkey is the local key
        this.catKey = catkey
        // this.showCards = false
        console.log('selectCategory... showcards = not changed')
      }
    },
    setTopicCards (topic, topickey) {
      this.resetShow()
      this.cards = []
      this.topic = topic
      this.topicKey = topickey
      console.log('Topickey...', this.topicKey)
      console.log('Topic.fbKey', this.topic.fbKey)
      console.log('Topic._parentPath', this.topic._parentPath)
      console.log('Topic._parentRecord', this.topic._parentRecord)
      let x = 'categories.' + this.topic._parentRecord + '.topics[' + this.topic.fbKey + ']'
      var val = _.get(this, x)
      console.log('x', x)
      console.log('_get', val)
      // this.newCard.topicID = this.topicKey
      this.newCard.topicID = this.topic.fbKey
      this.showCards = true
      // console.log('cardsfortopic ', this.cardsForTopic)
    },
    topicEdit () {
      this.resetShow()
      this.showTopicEdit = true
    },
    addTopic () {
      this.resetShow()
      this.showNewTopic = true
    },
    addCategory () {
      this.resetShow()
      this.showNewCategory = true
    },
    addCard () {
      this.resetShow()
      this.showNewCard = true
    },
    updateCard (card) {
      // updateRecord(object, fbKey)
      codeCards.updateRecord(card, card.fbKey, 'codecards')
    },
    postCard (card) {
      codeCards.postRecord(card, 'codecards')
      this.resetShow()
      this.showCards = true
    },
    postCategory (newCategory) {
      categories.postRecord(newCategory, 'categories')
      this.resetCategory()
    },
    postTopic (newTopic) {
      console.log('postTopic newTopic.categoryID ', newTopic.categoryID)
      // var records = this.categories.records[newTopic.categoryID].topics
      /* child2 example (newRecord, 'child1[c1Key].child2', child1.parentID) */
      this.categories.postRecord(newTopic, 'topics', newTopic.categoryID)
    },
    deleteTopic () {
      console.log('deleteTopic topic', this.topic)
      var fbKeyTopic = this.topic.fbKey
      var fbKeyCat = this.topic.categoryID
      console.log('deleteTopic fbKeyTopic, fbKeyCat ', fbKeyTopic, fbKeyCat)
      for (var key in this.cardsForTopic) {
        this.codeCards.deleteRecord(this.cardsForTopic[key], 'codecards')
        console.log('deleteTopic card ', this.cardsForTopic[key])
      }
      this.categories.deleteRecord(this.topic, 'topics')
    },
    getCategories () {
      this.resetShow()
      this.categories.getRecords('', 'categories.json')
      this.codeCards.getRecords('', 'codecards.json')
    },
    toggleLeftSidenav () {
      this.$refs.leftSidenav.toggle()
    },
    toggleRightSidenav () {
      this.$refs.rightSidenav.toggle()
    },
    closeRightSidenav () {
      this.$refs.rightSidenav.close()
    },
    open (ref) {
      console.log('Opened: ' + ref)
    },
    close (ref) {
      console.log('Closed: ' + ref)
    }
  }
}
</script>

<style scoped>
 .md-select-content {
   width: 100% !important;
 }
</style>



